import React from 'react';
import Image from 'gatsby-image';
import { useStaticQuery, graphql } from 'gatsby';

import Layout from '../../components/Layout';

const Gallery = () => {
	const data = useStaticQuery(graphql`
		query Images {
			# image: file(relativePath: {eq: "gatsby-astronaut.jpg"}) {
			#   id
			#   childImageSharp {
			#     fluid {
			#       ...GatsbyImageSharpFluid
			#     }
			#     fixed(width: 400) {
			#       ...GatsbyImageSharpFixed
			#     }
			#   }
			# }
			# unsplash1: file(relativePath: {eq: "gatsby-astronaut.jpg"}) {
			#   childImageSharp {
			#     fluid(quality: 100) {
			#       ...GatsbyImageSharpFluid_withWebp
			#     }
			#   }
			# }
			gallery: allFile(filter: { relativeDirectory: { eq: "gallery" } }) {
				nodes {
					id
					childImageSharp {
						fluid {
							...GatsbyImageSharpFluid
						}
						fixed(width: 200, height: 200) {
							...GatsbyImageSharpFixed
						}
					}
				}
			}
		}
	`);

	console.log(data);

	return (
		<Layout>
			<h2> Awesome gallery produced by gatsby-image</h2>
			{/* <Image fixed={data.image.childImageSharp.fixed} />
			<div style={{ width: '400px' }}>
				<Image fluid={data.unsplash1.childImageSharp.fluid} />
			</div> */}
			<div className='gallery'>
				{data.gallery.nodes.map((image, index) => (
					<Image fixed={image.childImageSharp.fixed} key={index} />
				))}
			</div>
		</Layout>
	);
};

export default Gallery;
